
.menu-item {
    max-width: calc(25% - 40px);
    width: 100%;
    text-align: center;
    margin: 10px 20px;
  }
  .menu-item a { 
    text-decoration: none;
    color: #000
  }
  
  .menu-item:hover {
    opacity: .75
  }
  
  .menu-item .Body-1-Bold {
    margin-bottom: 10px;
  }
  
  .menu-item .Body-1-Regular {
    margin-top: 0
  }
  
  .menu-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 85%;
    margin: 0 auto;
    margin-bottom: 50px;
  }
  
  .menu-container.circle .menu-item img {
    border-radius: 100%;
  }
  